<template>
	<div>
		<div id="my">
			<v-btn color="red" @click="cancel">Annuleren</v-btn>
			<v-btn color="green" @click="save">Opslaan</v-btn>
			<!-- Ingredient: {{ $route.params.id }} -->
			<v-flex xs12 sm6 md3>
				<v-text-field label="Ingrediënt" v-model="ingredient.name"></v-text-field>
			</v-flex>
		</div>
	</div>
</template>

<script>
import {globalStore} from '../main.js'

export default {
	data () {
		return {
			ingredient: {}
		}
	},
	created() {
		const id = this.$route.params.id;
		console.log("id", id);
		if (id === "new") {
			this.ingredient = {
				id: -1,
				name: ""
			};
		}
		else {
			this.ingredient = globalStore.ingredients.find(function(i) {
				return i.id == id;
			});
		}
	},
	methods: {
		save: function() {
			if (this.ingredient.id === -1) {
				const max_ingredient = globalStore.ingredients.reduce(function (p, v) {
					return (p.id > v.id ? p : v);
				});
				this.ingredient.id = max_ingredient.id + 1;
				globalStore.ingredients.push(this.ingredient);
			}
			this.$router.push("/ingredients");
		},
		cancel: function() {
			this.$router.push("/ingredients");
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#my {
	padding: 0px 24px 0px 24px;
}
</style>



