<template>
	<div>
		<div id="my">
			<v-btn color="red" @click="cancel">Annuleren</v-btn>
			<v-btn color="green" @click="save">Opslaan</v-btn>
			<!-- Ingredient: {{ $route.params.id }} -->
			<v-flex xs12 sm6 md3>
				<v-text-field label="Gerecht"></v-text-field>
			</v-flex>
			<v-flex xs12 sm6>
				<v-select v-model="value" item-text="name" item-value="id" :items="ingredients" attach chips label="Ingrediënten" multiple></v-select>
			</v-flex>
		</div>
	</div>
</template>

<script>
import {globalStore} from '../main.js'

export default {
	data () {
		return {
			ingredients: globalStore.ingredients
		}
	},
	created() {
		this.ingredients = globalStore.ingredients;
	},
	methods: {
		save: function() {
			this.$router.push("/dishes");
		},
		cancel: function() {
			this.$router.push("/dishes");
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#my {
	padding: 0px 24px 0px 24px;
}
</style>



