<template>
	<div id="app">
		<router-view id="router"/>
		<Menu></Menu>
	</div>
</template>

<script>
import Menu from "./components/Menu.vue"

export default {
	name: 'App',
	components: { Menu }
}
</script>

<style>
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	margin-top: 0px;
	height: 100%;
	width: 100%;
}

#router {
	height: calc(100% - 60px);
	overflow-y: hidden;
}

</style>



