<template>
	<div id="outer">
		<div id="top">
			<v-btn color="green" @click="clickNewDish">Nieuw</v-btn>
		</div>
		<v-list subheader id="list">
			<!-- <v-subheader>Ingrediënten</v-subheader> -->
			<v-list-tile v-for="item in items" :key="item.id" avatar @click="clickEditDish(item.id)">
				<v-list-tile-avatar>
					<v-icon>fa-hamburger</v-icon>
				</v-list-tile-avatar>

				<v-list-tile-content>
					<v-list-tile-title v-html="item.name"></v-list-tile-title>
				</v-list-tile-content>

				<v-list-tile-action>
					<!-- <v-icon color="teal">chat_bubble</v-icon> -->
				</v-list-tile-action>
			</v-list-tile>
		</v-list>
	</div>
</template>

<script>
import {globalStore} from '../main.js'

export default {
	data () {
		return {
			items: globalStore.dishes
		}
	},
	methods: {
		clickNewDish: function() {
			this.$router.push('/dishes/new')
		},
		clickEditDish: function(id) {
			this.$router.push('/dishes/' + id);
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#outer {
	height: 100%;
}

#top {
	text-align: left;
}

#list {
	height: calc(100% - 60px);
	overflow-y: auto;
}
</style>



