<template>
	<div>
		<v-bottom-nav :active.sync="bottomNav" :value="true" absolute color="transparent">
			<v-btn color="teal" flat value="/ingredients">
				<span>Ingrediënten</span>
				<v-icon>fas fa-carrot</v-icon>
			</v-btn>

			<v-btn color="teal" flat value="/dishes">
				<span>Gerechten</span>
				<v-icon>fa-hamburger</v-icon>
			</v-btn>

			<v-btn color="teal" flat value="/shoppinglist">
				<span>Boodschappen</span>
				<v-icon>fa-shopping-cart</v-icon>
			</v-btn>
		</v-bottom-nav>
	</div>
</template>

<script>
export default {
	data () {
		return {
			bottomNav: "ingredients"
		}
	},
	watch: {
		bottomNav: function(val, oldVal) {
			this.$router.push(val);
		}
	}
}
</script>

<style scoped>

</style>



