import '@fortawesome/fontawesome-free/css/all.css' // Ensure you are using css-loader
import 'vuetify/dist/vuetify.min.css'

import Vue from 'vue'
import Vuetify from 'vuetify'
import App from './App'
import router from './router'

Vue.config.productionTip = false
Vue.use(Vuetify, {
	iconfont: 'fa4'
});

/* eslint-disable no-new */
export const globalStore = new Vue({
	el: '#app',
	router,
	components: { App },
	template: '<App/>',
	data: {
		ingredients: [
			{ id: 0, name: 'Tomaten' },
			{ id: 1, name: 'Pasta' },
			{ id: 2, name: 'Aardappel' },
			{ id: 3, name: 'Sla' },
			{ id: 4, name: 'Sla' },
			{ id: 5, name: 'Sla' },
			{ id: 6, name: 'Sla' },
			{ id: 7, name: 'Komkommer' }
		],
		dishes: [
			{ id: 0, name: 'Pasta rode saus' },
			{ id: 1, name: 'Pasta witte saus' },
			{ id: 2, name: 'Falafel' },
			{ id: 3, name: 'Hamburger op brood' },
			{ id: 4, name: 'Patat' },
			{ id: 5, name: 'Pannenkoeken' },
			{ id: 6, name: 'Wraps' },
			{ id: 7, name: 'Gado gado' }
		]
	}
})



